'use client'; // Thêm directive này để đánh dấu là Client Component
import { createProduct } from "@/app/services/productService";
import { ProductDTO } from "@/app/types/ProductDTO";
import React, { useState } from "react";

export default  function CreateProductPage() {
    const [formData, setFormData] = useState<ProductDTO>({
        name:'',
        description:'',
        size:'',
        color:'',
        price:0,
        imageUrl: '',
    });

    const [error, setError] = useState<string | null>(null);
    const [success, setSuccess] = useState<string | null>(null);
    const [imagePreview, setImagePreview] = useState<string | null>(null); 
    const [imageFile, setImageFile] = useState<File | null>(null);
    const [isLoading, setIsLoading] = useState(false);

    // xử lý thay đổi input
    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
            setFormData((prev) => ({
            ...prev,
            [name]: name === 'price' ? parseFloat(value) : value, // chuyển price thành số
            }));
        };

        // Xử lý chọn file hình ảnh
        const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
                const file = e.target.files?.[0];
                if (file) {
                    setImageFile(file);
                    setImagePreview(URL.createObjectURL(file));
                } else {
                    setImageFile(null);
                    setImagePreview(null);
                }
            };



    // xử lý submit
    const handleSubmit = async (e:React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        try {
            const data = new FormData();
            data.append('name', formData.name);
            data.append('description', formData.description);
            data.append('size', formData.size);
            data.append('color', formData.color);
            data.append('price', formData.price.toString());
            if (imageFile) {
                data.append('image', imageFile);
            }
        } catch (error) {
            
        }
    }

    return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Tạo Sản Phẩm</h1>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="name" className="block">Tên sản phẩm</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            className="w-full border p-2"
            required
          />
        </div>
        <div>
          <label htmlFor="description" className="block">Mô tả</label>
          <textarea
            id="description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            className="w-full border p-2"
          />
        </div>
        <div>
          <label htmlFor="size" className="block">Kích thước</label>
          <input
            type="text"
            id="size"
            name="size"
            value={formData.size}
            onChange={handleChange}
            className="w-full border p-2"
            required
          />
        </div>
        <div>
          <label htmlFor="color" className="block">Màu sắc</label>
          <input
            type="text"
            id="color"
            name="color"
            value={formData.color}
            onChange={handleChange}
            className="w-full border p-2"
            required
          />
        </div>
        <div>
          <label htmlFor="price" className="block">Giá</label>
          <input
            type="number"
            id="price"
            name="price"
            value={formData.price}
            onChange={handleChange}
            className="w-full border p-2"
            required
          />
        </div>
        <div>
          <label htmlFor="imageUrl" className="block">URL hình ảnh</label>
          <input
            type="text"
            id="imageUrl"
            name="imageUrl"
            value={formData.imageUrl}
            onChange={handleChange}
            className="w-full border p-2"
            required
          />
        </div>
        <button type="submit" className="bg-blue-500 text-white px-4 py-2">
          Tạo sản phẩm
        </button>
      </form>
      {success && <p className="text-green-500 mt-4">{success}</p>}
      {error && <p className="text-red-500 mt-4">{error}</p>}
    </div>
    )
}